import { cloneDeep, flattenDeep } from 'lodash'
import { nanoid } from 'nanoid'
/**
 * @description 
 * @author 
 * @param obj 
 * @param type ObjectNumber...
 *  */
export const isType = (obj, type) => {
  if (typeof type === 'string')
    return Object.prototype.toString.call(obj) == `[object ${type}]`
  if (Array.isArray(type))
    return type.some(
      t => Object.prototype.toString.call(obj) == `[object ${t}]`
    )
}
/**
 * @description nanoIDuuid
 * @param prefix id
 *  */
export const getUUID = (prefix = 'uuid') => {
  return prefix + nanoid()
}

/**
 * @description window document,vue
 * @author 
 * @param binder window document  
 * @param vm vue 
 * @param evtName   
 * @param listener   
 * @param options 
 * @return 
 *  */
export const setEventListener = (binder, vm, evtName, listener, options) => {
  if (!binder || !vm || !evtName || !listener) {
    return
  }
  // vm
  const _listener = (...arg) => {
    let { width, height } = vm.$el.getBoundingClientRect()
    if (!width || !height) return
    listener(...arg)
  }
  const remove = () => {
    binder.removeEventListener(evtName, _listener)
  }
  const add = () => {
    remove()
    binder.addEventListener(evtName, _listener, options)
  }
  add()
  vm.$on('hook:mounted', add)
  vm.$on('hook:activated', add)
  vm.$on('hook:deactivated', remove)
  vm.$once('hook:beforeDestroy', () => {
    remove()
  })
  return remove
}

/**
 * @description keepalive
 * @author 
 * @param vm vue 
 * @param fun 
 *  */
export const setWatchRoute = (vm, fun) => {
  // 
  vm.__memory_route__ = { name: '' }
  vm.__unwatch__ = null
  function setWatch() {
    if (!vm.__unwatch__)
      vm.__unwatch__ = vm.$watch(
        '$route',
        () => {
          // 
          if (!vm.__memory_route__.name) {
            vm.__memory_route__ = vm.$route
            fun(vm.$route)
          } else {
            // namename
            if (vm.__memory_route__.name == vm.$route.name) {
              //,
              let jsonStr = JSON.stringify([
                vm.__memory_route__.params,
                vm.__memory_route__.query,
              ])
              let jsonStr2 = JSON.stringify([vm.$route.params, vm.$route.query])
              // namename
              if (jsonStr != jsonStr2) {
                vm.__memory_route__ = vm.$route
                fun(vm.$route)
              }
            }
          }
        },
        { deep: true, immediate: true }
      )
  }
  function offWatch() {
    vm.__unwatch__ && vm.__unwatch__()
    vm.__unwatch__ = null
  }
  setWatch()
  vm.$on('hook:activated', () => setWatch())
  vm.$on('hook:deactivated', () => offWatch())
}

/**
 * @description  
 * @author 
 * @param {*} source
 * @param {*} id
 * @param {*} pId
 * @param {*} children
 * @param {*} deepClone
 * @return {treeData,leafs,objById,flatData}
 * @description  treeData 
 * @description  leafs 
 * @description  objById idkey
 * @description  flatData  
 * @return  
 * @return {track} idID
 * @return {trigger} idID
 */
export const flatToTree = (
  source,
  id = 'id',
  pId = 'pId',
  children = 'children',
  deepClone = true
) => {
  if (!isType(source, 'Array')) {
    console.error('')
    return
  }
  let flatData = deepClone
    ? cloneDeep(source)
    : //  childrentracktrigger
      source.map(v => {
        delete v.children
        delete v.track
        delete v.trigger
        return v
      })
  try {
    let treeData = flatData.reduce((arr, item) => {
      //  
      if (!item.hasOwnProperty(id))
        throw `${JSON.stringify(
          item
        )},id:${id}`
      item[children] = item[children] || []
      item.track = item.track || [item[id]]
      item.trigger = item.trigger || []
      let parent = flatData.find(node => node[id] == item[pId])
      if (!parent) arr.push(item)
      else {
        // track:id
        parent.track = parent.track || [parent[id]]
        item.track.push(parent.track)
        // trigger:id
        parent.trigger = parent.trigger || []
        parent.trigger.push(item[id])
        parent.trigger.push(item.trigger)
        parent[children] = parent[children]
          ? [...parent[children], item]
          : [item]
      }
      return arr
    }, [])
    let leafs = []
    // id keytrigger,
    let objById = flatData.reduce((obj, item) => {
      item.trigger = flattenDeep(item.trigger)
      item.track = flattenDeep(item.track).reverse()
      item.level = item.track.length
      obj[item[id]] = item
      if (!item[children].length) leafs.push(item)
      return obj
    }, {})

    return {
      treeData,
      leafs,
      objById,
      flatData,
    }
  } catch (error) {
    console.error(error)
    return {
      treeData: [],
      leafs: [],
      objById: {},
      flatData: [],
    }
  }
}

/**
 * @description   ,idpId
 * @author 
 * @param {*} id
 * @param {*} pId
 * @param {*} defaultPid pid,pid
 * @param {*} custom 
 * @param {*} customFunction 
 *
 */
export const copyFlatData = ({
  source,
  id = 'id',
  pId = 'pId',
  defaultPid = '',
  custom = {},
  customFunction,
}) => {
  if (!isType(source, 'Array')) {
    console.error('')
    return
  }
  let flatData = cloneDeep(source)
  // get id  newId map
  let idMap = flatData.reduce((obj, item) => {
    obj[item[id]] = getUUID()
    return obj
  }, {})
  idMap = { ...idMap, ...custom }
  return flatData.map(v => {
    let oldObj = cloneDeep(v)
    v[id] = idMap[v[id]]
    if (v[pId] && idMap[v[pId]]) v[pId] = idMap[v[pId]]
    else v[pId] = defaultPid
    if (customFunction) customFunction({ current: v, oldObj, idMap })
    return v
  })
}
/**
 * @description  tab
 * @author 
 * @param {*} str1 string
 * @param {*} str1 string
 * @return Boolean
 */
export const equalsString = (str1, str2) => {
  return (
    isType(str1, 'String') &&
    isType(str2, 'String') &&
    str1.replace(/\s|\\n|\\r|\\t/gms, '') ==
      str2.replace(/\s|\\n|\\r|\\t/gms, '')
  )
}

//
/**
 * @description  dom
 * @author 
 * @param {*} el 
 * @param {*} attrs  
 * @param {*} appendToElement  ,document.head
 * @return Boolean
 */
export const asyncLoadElement = (
  elementName,
  attrs,
  appendToElement = document.head
) => {
  let id = attrs.id
  if (!elementName || (id && document.getElementById(id))) {
    return false
  }
  return new Promise(resolve => {
    let el = document.createElement(elementName)
    Object.keys(attrs).map(key => {
      el.setAttribute(key, attrs[key])
    })
    appendToElement.appendChild(el)
    el.onload = () => {
      resolve(true)
    }
  })
}

/**
 * @description   
 * @author 
 * @param {*} arr 
 * @param {*} fun itemindex
 *   @return Boolean,true
 */
export const arrayRemoveItem = (arr, fun) => {
  if (isType(arr, 'Array') && isType(fun, 'Function')) {
    let stay = arr.filter((v, i) => {
      return !fun(v, i)
    })
    arr.splice(0, arr.length)
    stay.forEach(v => {
      arr.push(v)
    })
  } else throw 'arrayRemoveItem arrfun'
}
